Meteor.methods({
	markAsDone: function (taskId) {
		var userId = Meteor.userId();
		check(taskId, String);
		Tasks.update(
			 {
				 _id: taskId,
			 },
			 {
				 $set:
					{
						effectiveEndDate: moment().format("DD.MM.YYYY HH:mm"),
						percentageDone: 100,
						realized: "Yes",
						markedAsDoneBy: userId,
						modifiedBy: userId,
						modifiedDate: moment().format("DD.MM.YYYY HH:mm"),
					},
			 }
		);


		if (typeof(toastr) !== 'undefined') {
			toastr.success(TAPi18n.__('Task marked as done'));
		}
	},
	updateTask: function (data) {
		var full = '';
		check(data, Match.Any);

		full = full.concat(data.taskContentNoHTML).concat(" ");
		full = full.concat(data.additionalTextNoHTML).concat(" ");
    full = full.concat(data.tagsNoHTML).concat(" ");
		full = full.concat(data.otherProjectsNoHTML).concat(" ");
		previousTask = Tasks.findOne(data.taskId);
		Tasks.update(
			{
			  $and: [
			  	{
						_id: data.taskId,
					},
			  ],
			},
			{
		      scheduledStartDate: data.scheduledStartDate,
		      effectiveStartDate: data.effectiveStartDate,
		      scheduledEndDate: data.scheduledEndDate,
		      effectiveEndDate: data.effectiveEndDate,
		      taskContent: data.taskContent,
					realized: data.realized,
					projectsIds: data.projectsIds,
					minutesIds: data.minutesIds,
					actioneeIds: data.actioneeIds,
					tags: data.tags,
					otherProjects: data.otherProjects,
					additionalText: data.additionalText,
					actionee: data.actionee,
					percentageDone: data.percentageDone,
					addedBy: previousTask.addedBy,
					addedDate: previousTask.addedDate,
					modifiedBy: Meteor.userId(),
					modifiedDate: moment().format("DD.MM.YYYY HH:mm"),
					full: full,
					searchResultFromTasks: true,
					showInGantt: data.showInGantt,
					sendReminder: data.sendReminder,
					sendReminderActionee: data.sendReminderActionee,
			},
			{
			    upsert: true,
			}
		);
	},
	duplicateTask: function (taskId) {
		var previousTask = Tasks.findOne(taskId);
		check(taskId, String);
		newTaskId = Tasks.insert(
			{
				scheduledStartDate: previousTask.scheduledStartDate,
				effectiveStartDate: previousTask.effectiveStartDate,
				scheduledEndDate: previousTask.scheduledEndDate,
			  effectiveEndDate: previousTask.effectiveEndDate,
				taskContent: previousTask.taskContent,
				realized: previousTask.realized,
				projectsIds: previousTask.projectsIds,
				minutesIds: previousTask.minutesIds,
				actioneeIds: previousTask.actioneeIds,
				tags: previousTask.tags,
				otherProjects: previousTask.otherProjects,
				additionalText: previousTask.additionalText,
				actionee: previousTask.actionee,
				percentageDone: previousTask.percentageDone,
				addedBy: Meteor.userId(),
				addedDate: moment().format("DD.MM.YYYY HH:mm"),
				full: previousTask.full,
				searchResultFromTasks: true,
				sendReminder: previousTask.sendReminder,
				sendReminderActionee: previousTask.sendReminderActionee,
			}
		);
		return newTaskId;
	},
  createTask: function (data) {
		var today = moment();
		var scheduledEndDate = moment().businessAdd(2, 'days');
		var userId = Meteor.userId();
    var user = Members.collection.findOne({accountId: userId});
    var username = user.profile.nickname;
		var taskId;
		var projectsIds = [];
		var minutesIds = [];
		var actioneeIds = [];
		check(data, Match.Any);
    RKCore.log(userId);
		RKCore.log(user);
		RKCore.log(username);
		if (typeof(data.projectsIds) !== 'undefined') {
			projectsIds = data.projectsIds;
		}
		if (typeof(data.minutesIds) !== 'undefined') {
			minutesIds = data.minutesIds;
		}
		taskId = Tasks.insert(
			{
				scheduledStartDate: moment(today).format("DD.MM.YYYY"),
				effectiveStartDate: "",
				scheduledEndDate: moment(scheduledEndDate).format("DD.MM.YYYY"),
			  effectiveEndDate: "",
				taskContent: "",
				realized: "No",
				projectsIds: projectsIds,
				minutesIds: minutesIds,
				actioneeIds: actioneeIds,
				tags: "",
				otherProjects: "",
				additionalText: "",
				actionee: username,
				percentageDone: 0,
				addedBy: Meteor.userId(),
				addedDate: moment().format("DD.MM.YYYY HH:mm"),
				full: "",
				searchResultFromTasks: true,
				sendReminder: false,
				sendReminderActionee: false,
			}
		);
		return taskId;
	},
	updateMinutes: function (data) {
		var full = '';
		check(data, Match.Any);

		full = full.concat(data.objective).concat(" ");
		full = full.concat(data.minutesName).concat(" ");
		full = full.concat(data.minutesTitle).concat(" ");
		full = full.concat(data.minutesContentNoHTML).concat(" ");
    full = full.concat(data.tagsNoHTML).concat(" ");
		previousMinutes = Minutes.findOne(data.minutesId);
		Minutes.update(
			{
			  $and: [
			  	{
						_id: data.minutesId,
					},
			  ],
			},
			{
		      location: data.location,
					minutesDate: data.minutesDate,
					author: data.author,
					recipients: data.recipients,
					objective: data.objective,
					attendeesPresent: data.attendeesPresent,
					minutesContent: data.minutesContent,
					minutesTitle: data.minutesTitle,
					minutesName: data.minutesName,
					projectsIds: data.projectsIds,
					tags: data.tags,
					addedBy: previousMinutes.addedBy,
					addedDate: previousMinutes.addedDate,
					modifiedBy: Meteor.userId(),
					modifiedDate: moment().format("DD.MM.YYYY HH:mm"),
					full: full,
					searchResultFromTasks: true,
			},
			{
			    upsert: true,
			}
		);

		Meteor.call('updateTasksTable', data.dataTaskTable, data.minutesId, data.projectsIds);
	},
	duplicateMinutes: function (minutesId) {
		var previousMinutes = Minutes.findOne(minutesId);
		check(minutesId, String);
		newMinutesId = Minutes.insert(
			{
				minutesContent: previousMinutes.minutesContent,
				location: previousMinutes.location,
				minutesDate: previousMinutes.minutesDate,
				attendeesPresent: previousMinutes.attendeesPresent,
				author: previousMinutes.author,
				recipients: previousMinutes.recipients,
				objective: previousMinutes.objective,
				projectsIds: previousMinutes.projectsIds,
				minutesTitle: previousMinutes.minutesTitle,
				minutesName: previousMinutes.minutesName,
				tags: previousMinutes.tags,
				addedBy: Meteor.userId(),
				addedDate: moment().format("DD.MM.YYYY HH:mm"),
				full: previousMinutes.full,
				searchResultFromMinutes: true,
			}
		);
		return newMinutesId;
	},
	duplicateProject: function (id) {
		var previous = Projects.findOne(id);
		check(id, String);
		newId = Projects.insert(
			{
				projectNumber: previous.projectNumber,
				projectName: previous.projectName,
				projectDescription: previous.projectDescription,
				projectManagerIds: previous.projectManagerIds,
				tags: previous.tags,
				addedBy: Meteor.userId(),
				addedDate: moment().format("DD.MM.YYYY HH:mm"),
				full: previous.full,
				searchResultFromProject: true,
			}
		);
		return newId;
	},
	updateProject: function (data) {
		var full = '';
		check(data, Match.Any);
		full = full.concat(data.projectNumber).concat(" ");
		full = full.concat(data.projectName).concat(" ");
		full = full.concat(data.projectDescription).concat(" ");
    full = full.concat(data.tagsNoHTML).concat(" ");
		previousProject = Projects.findOne(data.projectId);
		Projects.update(
			{
			  $and: [
			  	{
						_id: data.projectId,
					},
			  ],
			},
			{
		      projectNumber: data.projectNumber,
					projectName: data.projectName,
					projectDescription: data.projectDescription,
					projectManagerIds: data.projectManagerIds,
					tags: data.tags,
					addedBy: previousProject.addedBy,
					addedDate: previousProject.addedDate,
					modifiedBy: Meteor.userId(),
					modifiedDate: moment().format("DD.MM.YYYY HH:mm"),
					full: full,
					searchResultFromProject: true,
			},
			{
			    upsert: true,
			}
		);
	},
	createProject: function () {
		var projectId;
		var userId = Meteor.userId();
		projectId = Projects.insert(
			{
				projectNumber: "",
				projectName: "",
				projectDescription: "",
				projectManagerIds: [userId],
				addedBy: userId,
				addedDate: moment().format("DD.MM.YYYY HH:mm"),
				tags: "",
				full: "",
				searchResultFromProjects: true,
			}
		);
		return projectId;
	},
	createMinutes: function (data) {
		var minutesId;
		var userId = Meteor.userId();
    var user = Members.collection.findOne({accountId: userId});
    var username = user.profile.nickname;
		var projectsIds = [];
		check(data, Match.Any);
		if (typeof(data.projectsIds) !== 'undefined') {
			projectsIds = data.projectsIds;
		}
		minutesId = Minutes.insert(
			{
				minutesName: "",
				minutesTitle: "",
				location: "",
				minutesDate: moment().format("DD.MM.YYYY"),
				author: username,
				recipients: username,
				attendeesPresent: username,
				objective: "",
				minutesContent: "",
				projectsIds: projectsIds,
				addedBy: Meteor.userId(),
				addedDate: moment().format("DD.MM.YYYY HH:mm"),
				tags: "",
				full: "",
				searchResultFromMinutes: true,
			}
		);
		return minutesId;
	},
	updateTasksTable: function (data, minutesId, projectsIds) {
		var i;
    var arrayLength = data.length;
		var today = moment();
		var full = "";
		var scheduledEndDate;
		var actionee;
		check(data, Match.Any);
		check(minutesId, String);
		check(projectsIds, [String]);
    for (i = 0; i < arrayLength; i++) {
      RKCore.log(data[i]);
			full = "";
			if (!data[i].scheduledEndDate) {
				scheduledEndDate = "";
			}
			else {
				scheduledEndDate = data[i].scheduledEndDate;
			}
			if (!data[i].actionee) {
				actionee = "";
			}
			else {
				actionee = data[i].actionee;
			}
			if (data[i].taskContent) {
				full = full.concat(data[i].taskContent).concat(" ");
				Tasks.insert(
					{
						scheduledStartDate: moment(today).format("DD.MM.YYYY"),
						effectiveStartDate: "",
						scheduledEndDate: scheduledEndDate,
					  effectiveEndDate: "",
						taskContent: data[i].taskContent,
						realized: "No",
						projectsIds: projectsIds,
						minutesIds: [minutesId],
						tags: "",
						additionalText: "",
						actionee: actionee,
						percentageDone: 0,
						addedBy: Meteor.userId(),
						addedDate: moment().format("DD.MM.YYYY HH:mm"),
						full: full,
						searchResultFromTasks: true,
					}
				);
			}
    }
	},
	saveTasksTable: function (data) {
		var i;
    var arrayLength = data.length;
		var today = moment();
		var full = "";
		var scheduledEndDate;
		var actionee;
		check(data, Match.Any);
    for (i = 0; i < arrayLength; i++) {
      RKCore.log(data[i]);
			full = "";
			if (!data[i].scheduledEndDate) {
				scheduledEndDate = "";
			}
			else {
				scheduledEndDate = data[i].scheduledEndDate;
			}
			if (!data[i].actionee) {
				actionee = "";
			}
			else {
				actionee = data[i].actionee;
			}
			if (data[i].taskContent) {
				full = full.concat(data[i].taskContent).concat(" ");
				full = full.concat(data[i].includedInMinutes).concat(" ");
				Tasks.insert(
					{
						scheduledStartDate: moment(today).format("DD.MM.YYYY"),
						effectiveStartDate: "",
						scheduledEndDate: scheduledEndDate,
					  effectiveEndDate: "",
						taskContent: data[i].taskContent,
						realized: "No",
						tags: "",
						additionalText: "",
						actionee: actionee,
						percentageDone: 0,
						addedBy: Meteor.userId(),
						addedDate: moment().format("DD.MM.YYYY HH:mm"),
						full: full,
						searchResultFromTasks: true,
					}
				);
			}
    }
	},
});
