if (Meteor.isServer) {
		sendReminders = function () {
	      	Meteor.call('sendReminders');
		};
		sendRemindersActionee = function () {
	      	Meteor.call('sendRemindersActionee');
		};

		SyncedCron.add({
			  name: 'Send task event reminders',
			  schedule: function (parser) {
			    return parser.text("at 08:00 am");
					//return parser.text("every 1 minute");
			  },
			  job: sendReminders,
		});

		SyncedCron.add({
			  name: 'Send task event reminders to actionee',
			  schedule: function (parser) {
			    return parser.text("at 08:01 am");
					//return parser.text("every 1 minute");
			  },
			  job: sendRemindersActionee,
		});

		Meteor.methods({
			sendReminders: function () {
				var html;
				var editLink;
				allTasks = Tasks.find(
					{
						$and: [
							{
								sendReminder: true,
							},
							{
								scheduledEndDate: moment().format('DD.MM.YYYY'),
							},
						],
					}
				).fetch();

				nTasks = allTasks.length;
				RKCore.log("I will send " + nTasks + " email(s)");

				for (i = 0; i < nTasks; i++) {
					html = '<p><strong>' + TAPi18n.__("Task content") + ' :</strong>' + allTasks[i].taskContent + '</p>';

					editLink = '<p><a href="' +
						Router.routes.editTask.url({_id: allTasks[i]._id})
						+ '" title="' + TAPi18n.__('Edit') + '"'
						+ '>'
						+ Router.routes.editTask.url({_id: allTasks[i]._id})
						+ '</a></p>';
					html = html + editLink;

					html = html + '<p>' + TAPi18n.__('Scheduled end date') + ' :' + allTasks[i].scheduledEndDate + '</p>';

					html = html + '<p>' + TAPi18n.__('You are receiving this email because you are author of this task') + '.</p>';

					Email.send({
		        from: Meteor.settings.rationalK_mail.from,
		        to: Members.collection.findOne({accountId: allTasks[i].addedBy}).email,
		        subject: "[" + Meteor.settings.public.header_text + "] " + TAPi18n.__("Task reminder"),
		        html: html,
		      });
				}
		    return true;
		},
		sendRemindersActionee: function () {
			var html;
			var editLink;
			allTasks = Tasks.find(
				{
					$and: [
						{
							sendReminderActionee: true,
						},
						{
							scheduledEndDate: moment().format('DD.MM.YYYY'),
						},
					],
				}
			).fetch();

			nTasks = allTasks.length;
			RKCore.log("I will send " + nTasks + " email(s) to the actionee");

			for (i = 0; i < nTasks; i++) {
				senderMember = Members.collection.findOne({accountId: allTasks[i].addedBy});
				sender = {
					name: senderMember.profile.name,
					address: senderMember.email,
				};
				if (typeof(senderMember.profile.locale) !== 'undefined') {
					senderLang = senderMember.profile.locale;
				}
				else {
					senderLang = 'en';
				}
				RKCore.log("senderLang : ");
				RKCore.log(senderLang);
				//RKCore.log(TAPi18n.getLanguages());
				RKCore.log(TAPi18n.__("Task content", {}, senderLang));
				senderText = '"' + senderMember.profile.name + '" <' + senderMember.email + '>';
				RKCore.log("sender :");
				RKCore.log(sender);

				html = '<p><strong>' + TAPi18n.__("Task content", {}, senderLang) + ' :</strong>' + allTasks[i].taskContent + '</p>';

				editLink = '<p><a href="' +
					Router.routes.editTask.url({_id: allTasks[i]._id})
					+ '" title="' + TAPi18n.__('Edit', {}, senderLang) + '"'
					+ '>'
					+ Router.routes.editTask.url({_id: allTasks[i]._id})
					+ '</a></p>';
				html = html + editLink;

				html = html + '<p>' + TAPi18n.__('Scheduled end date', {}, senderLang) + ' :' + allTasks[i].scheduledEndDate + '</p>';

				html = html + '<p>' + TAPi18n.__('You are receiving this email because you have been listed as an actionee for this task by', {}, senderLang) + " " + senderMember.profile.name + '.</p>';

				RKCore.log("html : ");
				RKCore.log(html);

				actioneeIds = allTasks[i].actioneeIds;
				RKCore.log("actioneeIds : ");
				RKCore.log(actioneeIds);
				nActionee = actioneeIds.length;
				to = [];
				recipients = '';
				for (j = 0; j < nActionee; j++) {
					actionee = Members.collection.findOne({_id: actioneeIds[j]});
					to.push({
						name: actionee.profile.name,
						address: actionee.email,
					});
					recipients = recipients + '"' + actionee.profile.name + '" <' + actionee.email + '>,';
				}
				recipients = recipients.slice(0, - 1);
				RKCore.log("to : ");
				RKCore.log(to);
				RKCore.log("recipients : ");
				RKCore.log(recipients);
				Email.send({
					from: senderText,
					to: recipients,
					cc: senderText,
					replyTo: senderText,
					subject: "[" + Meteor.settings.public.header_text + "] " + TAPi18n.__("Task reminder", {}, senderLang),
					html: html,
				});
			}
			return true;
	},
	});
} // end of is Server check
