if (Meteor.settings.public.show.tasks) {
  Router.route("/tasks/:filterBy?/:filterVal?", {
    name: "tasks",
    waitOn: function () {
      RKCore.log("this.params.filterBy : " + this.params.filterBy);
      RKCore.log("this.params.filterVal : " + this.params.filterVal);
      if (this.params.filterBy === "project") {
        Meteor.subscribe("tasksByProjectId", this.params.filterVal);
      }
      else if (this.params.filterBy === "author") {
        Meteor.subscribe("tasksByAuthorId", this.params.filterVal);
      }
      else if (this.params.filterBy === "authorUserId") {
        //les taches ou je suis autheur
        Meteor.subscribe("authorTasksByUserId");
      }
      else if (this.params.filterBy === "actionee") {
        Meteor.subscribe("tasksByActioneeId", this.params.filterVal);
      }
      else if (this.params.filterBy === "minutes") {
        Meteor.subscribe("tasksByMinutesId", this.params.filterVal);
      }
      else {
        Meteor.subscribe("tasks");
      }
      return [
        Meteor.subscribe("projects"),
        Meteor.subscribe("members"),
        Meteor.subscribe("allMinutes"),
      ];
    },
  });

  Router.route("/projects", {
    name: "viewProjects",
    waitOn: function () {
      Meteor.subscribe("projects");
      Meteor.subscribe("tasks");
      return [Meteor.subscribe("members")];
    },
  });

  Router.route("/project/edit/:_id", {
    name: "editProject",
    data: function () {
      var usernames = [];
      var members = Members.collection.find().fetch();
      var data = {};
      var nMembers = members.length;
      var i;
      data.currentProject = Projects.findOne(this.params._id);

      for (i = 0; i < nMembers; i++) {
          usernames.push(members[i].profile.nickname);
      }
      data.usernames = usernames;
      return data;
    },
    waitOn: function () {
      return [
        Meteor.subscribe("project", this.params._id),
        Meteor.subscribe("minutesByProjectId", this.params._id),
        Meteor.subscribe("tasksByProjectId", this.params._id),
        Meteor.subscribe("members"),
        Meteor.subscribe("tags"),
       ];
    },
  });


  Router.route("/task/edit/:_id/:filterBy?/:filterVal?", {
    name: "editTask",
    data: function () {
      var usernames = [];
      var members = Members.collection.find().fetch();
      var data = {};
      var nMembers = members.length;
      var i;
      data.currentTask = Tasks.findOne(this.params._id);

      for (i = 0; i < nMembers; i++) {
          usernames.push(members[i].profile.nickname);
      }
      data.usernames = usernames;
      data.filterBy = this.params.filterBy;
      data.filterVal = this.params.filterVal;
      RKCore.log(data);
      return data;
    },
    waitOn: function () {
      return [
        Meteor.subscribe("task", this.params._id),
        Meteor.subscribe("members"),
        Meteor.subscribe("projects"),
        Meteor.subscribe("allMinutes"),
        Meteor.subscribe("tags"),
       ];
    },
  });

  Router.route("/task/table/edit", {
    name: "editTasksTable",
    data: function () {
      return true;
    },
    waitOn: function () {
      return [
        Meteor.subscribe("members"),
       ];
    },
  });

  Router.route("/minutes/:filterBy?/:filterVal?", {
    name: "minutes",
    waitOn: function () {
      RKCore.log("this.params.filterBy : " + this.params.filterBy);
      RKCore.log("this.params.filterVal : " + this.params.filterVal);
      if (this.params.filterBy === "project") {
        // filterVal = projectName
        Meteor.subscribe("minutesByProjectId", this.params.filterVal);
      }
      else if (this.params.filterBy === "author") {
        // filterVal = authorId
        Meteor.subscribe("minutesByAuthorId", this.params.filterVal);
      }
      else {
        Meteor.subscribe("allMinutes");
      }
      return [
        Meteor.subscribe("members"),
        Meteor.subscribe("projects"),
      ];
    },
  });

  Router.route("/editMinutes/:_id/:filterBy?/:filterVal?", {
    name: "editMinutes",
    data: function () {
      var usernames = [];
      var members = Members.collection.find().fetch();
      var data = {};
      var nMembers = members.length;
      var i;
      data.currentMinutes = Minutes.findOne(this.params._id);

      for (i = 0; i < nMembers; i++) {
          usernames.push(members[i].profile.nickname);
      }
      data.usernames = usernames;
      data.filterBy = this.params.filterBy;
      data.filterVal = this.params.filterVal;
      return data;
    },
    waitOn: function () {
      return [
        Meteor.subscribe("minutes", this.params._id),
        Meteor.subscribe("members"),
        Meteor.subscribe("projects"),
        Meteor.subscribe("tags"),
        Meteor.subscribe("SettingsRKTasks"),
        Meteor.subscribe("tasksByMinutesId", this.params._id),
       ];
    },
  });


  Router.route("/print/task/:_id/:serverToken?/:serverTokenVal?", {
    name: "printTask",
    layoutTemplate: 'printLayout',
    data: function () {
      var usernames = [];
      var members = Members.collection.find().fetch();
      var data = {};
      var nMembers = members.length;
      var i;
      data.currentTask = Tasks.findOne(this.params._id);

      for (i = 0; i < nMembers; i++) {
          usernames.push(members[i].profile.nickname);
      }
      data.usernames = usernames;
      return data;
    },
    waitOn: function () {
      return [
        Meteor.subscribe("members"),
        Meteor.subscribe("tags"),
        Meteor.subscribe("allMinutes"),
        Meteor.subscribe("task", this.params._id),
        Meteor.subscribe("projects"),
       ];
    },
  });

  Router.route("/print/minutes/:_id/:serverToken?/:serverTokenVal?", {
    name: "printMinutes",
    layoutTemplate: 'printLayout',
    data: function () {
      var usernames = [];
      var members = Members.collection.find().fetch();
      var data = {};
      var nMembers = members.length;
      var i;
      data.currentMinutes = Minutes.findOne(this.params._id);

      for (i = 0; i < nMembers; i++) {
          usernames.push(members[i].profile.nickname);
      }
      data.usernames = usernames;
      return data;
    },
    waitOn: function () {
      return [
        Meteor.subscribe("minutes", this.params._id),
        Meteor.subscribe("members"),
        Meteor.subscribe("tags"),
        Meteor.subscribe("tasksByMinutesId", this.params._id),
       ];
    },
  });

  Router.route("/followup", {
    name: "followup",
    waitOn: function () {
      return [
        Meteor.subscribe("followup"),
        Meteor.subscribe("members"),
      ];
    },
  });
}
