Meteor.methods({
  getSettingsRKTasks: function (name) {
    var value = '';
    var settings;
    check(name, String);
    RKCore.log("getSettingsRKTasks server method");
    RKCore.log(SettingsRKTasks.find({}).fetch());
    settings = SettingsRKTasks.findOne({name: name});
    if (typeof(settings) !== 'undefined') {
      value = settings.value;
    }
    RKCore.log("value :");
    RKCore.log(value);
    return value;
  },
  updateSettingsRKTasks: function (data) {
    RKCore.log(data);
    check(data, Match.Any);
    if (typeof(data.prefixMinutes) !== 'undefined') {
      SettingsRKTasks.update(
  			{
  			  name: "prefixMinutes",
  			},
        {
          $set: {value: data.prefixMinutes},
        },
        {
  			  upsert: true,
  			}
      );
    }
    if (typeof(data.folderPathForMinutesPrint) !== 'undefined') {
      SettingsRKTasks.update(
  			{
  			  name: "folderPathForMinutesPrint",
  			},
        {
          $set: {value: data.folderPathForMinutesPrint},
        },
        {
  			  upsert: true,
  			}
      );
    }
    RKCore.log(SettingsRKTasks.find({}).fetch());
    return true;
  },
});
