Meteor.publish("minutesTasks", function (minutesName) {
  var regExp = new RegExp("(" + minutesName + ")", "ig");
  check(minutesName, String);
  RKCore.log("Filter by minutesName :" + minutesName);
  return Tasks.find({ includedInMinutes: regExp });
});

Meteor.publish("minutesTasksByMinutesId", function (minutesId) {
  var myMinutes = Minutes.findOne(minutesId);
  var minutesName = myMinutes.minutesName;
  var regExp = new RegExp("(" + minutesName + ")", "ig");
  check(minutesId, String);
  RKCore.log("Filter by minutesName :" + minutesName);
  return Tasks.find({ includedInMinutes: regExp });
});

Meteor.publish("tasksByMinutesId", function (minutesId) {
  check(minutesId, String);
  return Tasks.find({ minutesIds: {$in: [minutesId]} });
});

