Meteor.publish("authorTasks", function (author) {
  var member = Members.collection.findOne({"profile.nickname": author});
  var accountId;
  check(author, String);
  RKCore.log("Filtre par author :" + author);
  if (typeof member !== 'undefined') {
    accountId = member.accountId;
    RKCore.log("accountId :" + accountId);
    return Tasks.find({ addedBy: accountId });
  }
  return [];
});

Meteor.publish("authorTasksByUserId", function () {
  var connectedUser = this.userId;
  var member = Members.collection.findOne({accountId: connectedUser});
  var accountId;
  RKCore.log("connectedUser : ");
  RKCore.log(connectedUser);
  RKCore.log("Filtre par author account Id :" + connectedUser);
  if (typeof member !== 'undefined') {
    accountId = member.accountId;
    RKCore.log("accountId :" + accountId);
    return Tasks.find({ addedBy: accountId });
  }
  return [];
});

