Meteor.methods({
  getMyTasksToDo: function () {
    var tasks = '';
    var userId = Meteor.userId();
    var memberId;
    var nTasks;
    var i;
    var formattedProjectNames = [];
    var formattedActioneeNames = [];
    var formattedMinutesNames = [];
    memberId = Members.collection.findOne({accountId: userId})._id;
    tasks = Tasks.find(
      {
        $and: [
          {
            actioneeIds: {
              $in: [memberId],
            },
          },
          {
            realized: "No",
          },
        ],
      }).fetch();

    nTasks = tasks.length;

    for (i = 0; i < nTasks; i++) {
      nMinutesIds = tasks[i].minutesIds.length;
      formattedMinutesNames = [];
      for (j = 0; j < nMinutesIds; j++) {
        minutes = Minutes.findOne(tasks[i].minutesIds[j]);
        formattedMinutesNames.push(minutes.minutesName);
      }
      tasks[i].formattedMinutesNames =  formattedMinutesNames;

      nProjectIds = tasks[i].projectsIds.length;
      formattedProjectNames = [];
      for (j = 0; j < nProjectIds; j++) {
        project = Projects.findOne(tasks[i].projectsIds[j]);
        formattedProjectNames.push(project.projectNumber);
      }
      tasks[i].formattedProjectNames =  formattedProjectNames;

      nActioneeIds = tasks[i].actioneeIds.length;
      formattedActioneeNames = [];
      for (j = 0; j < nActioneeIds; j++) {
        member = Members.collection.findOne(tasks[i].actioneeIds[j]);
        formattedActioneeNames.push(member.profile.nickname);
      }
      tasks[i].formattedActioneeNames =  formattedActioneeNames;
    }


    RKCore.log("tasks object sent to dashboard : ");
    RKCore.log(tasks);

    return tasks;
  },
});
