if (Meteor.isServer) {
	if (typeof Tasks.createIndex === 'function') {
		Tasks.createIndex({ full: "text" }, { name: "TextIndex" });
	}
	else {
		if (typeof Tasks._ensureIndex === 'function') {
			Tasks._ensureIndex( { full: "text" }, {name: "TextIndex"});
		}
	}

	if (typeof Minutes.createIndex === 'function') {
		Minutes.createIndex({ full: "text" }, { name: "TextIndex" });
	}
	else {
		if (typeof Minutes._ensureIndex === 'function') {
			Minutes._ensureIndex( { full: "text" }, {name: "TextIndex"});
		}
	}

	if (typeof Projects.createIndex === 'function') {
		Projects.createIndex({ full: "text" }, { name: "TextIndex" });
	}
	else {
		if (typeof Projects._ensureIndex === 'function') {
			Projects._ensureIndex( { full: "text" }, {name: "TextIndex"});
		}
	}
} //end if Server

