Tasks.after.update(function (userId, doc) {
  var data = {};
  var historyType = "taskUpdated";
  var user = Members.collection.findOne({accountId: userId});
  var username = user.profile.nickname;
  data.doc = doc;
  data.previous = this.previous;
  data.package = "rationalk:tasks";
  text = TAPi18n.__("A task has been modified");
  Meteor.history(text, data, historyType, doc._id);

  if ((this.previous.realized === "No" ) && (doc.realized === "Yes")) {
    html = '<p><strong>' + TAPi18n.__("Task content") + ' :</strong>' + doc.taskContent + '</p>';
    html = html + '<p><strong>' + TAPi18n.__("Marked as done by") + ' :</strong>' + username + '</p>';

    editLink = '<p><a href="' +
      Router.routes.editTask.url({_id: doc._id})
      + '" title="' + TAPi18n.__('Edit') + '"'
      + '>'
      + Router.routes.editTask.url({_id: doc._id})
      + '</a></p>';
    html = html + editLink;

    html = html + '<p>' + TAPi18n.__('Scheduled end date') + ' :' + doc.scheduledEndDate + '</p>';

    html = html + '<p>' + TAPi18n.__('You are receiving this email because you are author of this task') + '.</p>';

    Email.send({
      from: Meteor.settings.rationalK_mail.from,
      to: Members.collection.findOne({accountId: doc.addedBy}).email,
      subject: "[" + Meteor.settings.public.header_text + "] " + TAPi18n.__("Task marked as done"),
      html: html,
    });
  }
});
