Meteor.methods({
  generatePDF: function (data) {
    var fileName = "print.pdf";
    var url = "http://rationalk.ch";
    var set = SettingsRKTasks.findOne({name: "folderPathForMinutesPrint"});
    var options = {
      renderDelay: 5000,
      "paperSize": {
        "format": "Letter",
        "orientation": "portrait",
        "margin": "1cm",
      },
    };
		if (typeof(set) !== 'undefined') {
			folderPathForMinutesPrint = set.value;
      // Remove last trailing slash if any :
      folderPathForMinutesPrint = folderPathForMinutesPrint.replace(/\/$/, "");
		}
    else {
      folderPathForMinutesPrint = "~/Downloads";
    }
    if (typeof(data.fileName) !== 'undefined') {
      fileName = data.fileName;
    }
    filePath = folderPathForMinutesPrint + "/" + fileName;
    if (typeof(data.url) !== 'undefined') {
      url = data.url + '/serverToken/1234';
      //var url = "http://localhost:3000/print/minutes/6ZiBTbyC5ddmhi9nX/serverToken/1234";
    }
    check(data, Match.Any);
    webshot(url, filePath, options, function (err) {
        RKCore.log("Screenshot (" + url + ") saved : " + filePath);
        if (err) {
          RKCore.log("err : ");
          RKCore.log(err);
        }
      });
    return filePath;
  },
});
