RKTasks = {};
RKTasks.Collections = {};
RKCore.searchResultsPackage.push(
  {
    name: "RKTasks", // RKExperts.findAllFullTextSearch should exists
  }
);

RKTasks.findAllFullTextSearch = function () {
  return Tasks.find({}, {sort: {score: -1}}).fetch();
};

RKTasks.findFullText = function (searchQuery) {
  var sr;
  check(searchQuery, String);
  sr = Tasks.find(
    {
      $text: {
        $search: searchQuery,
      },
    },
    {
      fields: { score: { $meta: 'textScore' } },
      sort: { score: { $meta: 'textScore' } },
      limit: 30,
    });
    return sr;
};

RKTasks.findDummy = function () {
  return Tasks.find({$text: { $search: "somethingthatyouwillneverfind" }});
};

Meteor.methods({
	deleteTask: function (taskId) {
		check(taskId, String);
		Tasks.remove(
			{
			  _id: taskId,
			}
		);
		if (typeof(toastr) !== 'undefined') {
			toastr.success(TAPi18n.__('Task deleted succesfully'));
		}
	},
  deleteMinutes: function (minutesId) {
		check(minutesId, String);
		Minutes.remove(
			{
			  _id: minutesId,
			}
		);
		if (typeof(toastr) !== 'undefined') {
			toastr.success(TAPi18n.__('Minutes deleted succesfully'));
		}
	},
  deleteProject: function (projectId) {
		check(projectId, String);
		Projects.remove(
			{
			  _id: projectId,
			}
		);
		if (typeof(toastr) !== 'undefined') {
			toastr.success(TAPi18n.__('Project deleted succesfully'));
		}
	},
});
